"use client"

import * as React from "react"

import { cn } from "@/lib/utils"
import { Input } from "@/components/ui/input"

export function DateTimeRangePicker({ from, to, onFromChange, onToChange, className }) {
  const fromId = React.useId()
  const toId = React.useId()

  const handleFromChange = React.useCallback(
    (event) => {
      onFromChange?.(event.target.value)
    },
    [onFromChange]
  )

  const handleToChange = React.useCallback(
    (event) => {
      onToChange?.(event.target.value)
    },
    [onToChange]
  )

  return (
    <div className={cn("flex flex-wrap items-end gap-3", className)}>
      <div className="flex flex-col gap-1">
        <label htmlFor={fromId} className="text-[11px] font-medium uppercase tracking-wide text-muted-foreground">
          From
        </label>
        <Input
          id={fromId}
          type="datetime-local"
          value={from ?? ""}
          onChange={handleFromChange}
          max={to ?? undefined}
          step={60}
          className="h-8 w-48"
        />
      </div>
      <div className="flex flex-col gap-1">
        <label htmlFor={toId} className="text-[11px] font-medium uppercase tracking-wide text-muted-foreground">
          To
        </label>
        <Input
          id={toId}
          type="datetime-local"
          value={to ?? ""}
          onChange={handleToChange}
          min={from ?? undefined}
          step={60}
          className="h-8 w-48"
        />
      </div>
    </div>
  )
}
